.dashboard-wrapper {
  .title {
    font-size: 16px;
    text-align: center;
    margin: 12px 0;
    color: #3da742;
    margin-bottom: 30px;
  }
  .block-item {
    margin-bottom: 32px;
    > h3 {
      border-left: 3px solid #3da742;
      padding-left: 6px;
      font-size: 18px;
      font-weight: 600;
      color: #333;
    }
  }
  .overview {
    position: relative;

    .line {
      margin: 12px 0;
      .count {
        color: #3da742;
        opacity: 0.96;
        font-weight: 500;
      }
    }
  }
  .chart-block {
    .desc {
      color: #666;
      font-size: 14px;
    }
  }
}
